<template>
    <div class="deleteBanner">
        HelloDelete
    </div>
</template>

<script>
    export default {
        name: "DeleteBanner",
        methods: {
            deleteBanner() {
                http
                    .delete("/admin/deleteBanner/" + this.banner.id)
                    .then(response => {
                        console.log(response.data);
                        this.$emit("refreshData");
                        this.$router.push('/');
                    })
                    .catch(e => {
                        console.log(e);
                    });
            },
        }
    };
</script>

<style scoped>

</style>


